body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  display: block;
  width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
  margin: 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.nombre-pagina {
  text-align: center;
  margin: 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header .contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  display: none;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    display: inline-block;
    text-align: left;
    color: #FFFFFF;
    font-weight: 700;
    line-height: 2;
    max-width: 60rem;
    font-size: 3.2rem;
    margin: 0;
  }
}

.navegacion-flex {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion-flex {
    align-items: flex-end;
    flex-direction: column;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 2rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
  }
}

.imgLogo {
  height: 10rem;
  width: 10rem;
}
@media (min-width: 768px) {
  .imgLogo {
    margin: 0 0 0 7rem;
  }
}

.navegacion {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    margin-top: 0;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #329f00;
}
.navegacion .iconoNav {
  display: none;
}
@media (min-width: 768px) {
  .navegacion .iconoNav {
    display: block;
    height: 5rem;
    width: 5rem;
  }
}

.contenido-venta {
  border: 1px solid gray;
  padding: 1rem 0 3rem 0;
  width: 100%;
  box-shadow: 0px 6px 6px 0px rgba(112, 112, 112, 0.42);
}
.contenido-venta .venta__mesas {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  gap: 2rem;
  padding: 5rem;
}

.venta__mesa--diseño {
  height: 10rem;
  width: 100%;
  display: grid;
  align-items: center;
}
.venta__mesa--diseño2 {
  height: 10rem;
  width: 100%;
  display: grid;
  align-items: center;
  background-color: rgb(161, 245, 128);
  box-shadow: 0px 5px 5px 0px rgb(92, 252, 0);
  border: none;
}
.venta__mesa--diseño2 h2 {
  font-size: 1.8rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .venta__mesa--diseño2 h2 {
    font-size: 2rem;
  }
}
.venta__mesa--diseño2:hover {
  width: 90%;
  height: 8rem;
  cursor: pointer;
}

.mostrarCobro {
  display: block;
}

.ocultarCobro {
  display: none;
}

.actual {
  background-color: #329f00;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  font-size: 1.5rem;
}
table.propiedades thead {
  background-color: #329f00;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
  font-weight: 700;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.8rem;
}
table.propiedades tbody {
  text-align: center;
}
table.propiedades tr {
  height: 2rem;
  margin-bottom: 1rem;
}

h1 {
  margin-top: 2rem;
}

.input {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}

.tdBotones {
  display: grid;
  gap: 1rem;
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
  margin: 1rem 0 2rem 0;
}

.inputVacio {
  padding: 1rem;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
