}
@media (min-width: 768px) {
  .inputVacio {
    width: 20rem;
  }
}

.form {
  display: flex;
  justify-content: right;
  align-items: center;
}

.id-producto {
  font-weight: 700;
  font-size: 1.5rem;
}

.nombre-producto {
  font-size: 2rem;
}

.acciones-producto {
  display: grid;
  justify-content: center;
}

.botonesAdmin {
  padding: 0;
}
@media (min-width: 768px) {
  .botonesAdmin {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .botonesAdmin .boton2 {
    width: 100%;
  }
}

.listado-productos {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  place-items: center;
}
@media (min-width: 768px) {
  .listado-productos {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-productos .producto {
  background-color: #f1e576;
  padding: 1rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
  height: 100%;
  display: grid;
  align-items: end;
}
.listado-productos .producto.seleccionado {
  background-color: #0da6f3;
}
.listado-productos .producto.seleccionado p {
  color: #FFFFFF;
}
.listado-productos .producto:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.listado-productos .producto p {
  color: #1a1b15;
  margin: 0 0 1.25rem 0;
}
.listado-productos .producto .nombre-producto {
  color: #1a1b15;
  font-weight: 700;
  font-size: 1.4rem;
  font-family: Arial, Helvetica, sans-serif;
}
.listado-productos .producto .precio-producto {
  color: #0da6f3;
  font-weight: 700;
  margin: 0;
  font-size: 1.4rem;
  font-family: Arial, Helvetica, sans-serif;
}
.listado-productos .producto form .boton {
  background-color: rgb(252, 197, 48);
}
.listado-productos .producto .cantidad-producto {
  width: 100%;
}

.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
.orden {
  display: grid;
}
@media (min-width: 768px) {
  .orden {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
